const app = getApp();
import helper from '../common/helper.js';
import uniPopup from '@/components/uni-popup.vue';
import { pathToBase64, base64ToPath } from '../components/index.js';
export default {
	data() {
		return {
			bottomData: [
				{
					text: '微信好友',
					type: 'WXSceneSession',
					icon: 'iconweixin'
				},
				{
					text: '朋友圈',
					type: 'WXSenceTimeline',
					icon: 'iconpengyouquan'
				}
			],
			codeType: '',
			codeList: {},
			showIs: '0',
			openSettingBtnHidden: true, //是否授权,
			imgUrl: '',
			canvasWidth: '',
			canvasHeight: ''
		};
	},
	components: {
		// wTable,
		uniPopup
	},
	onLoad(option) {
		console.log(option);
		this.codeType = option.type;
		this.imgUrl = helper.imgUrl;
		if (this.codeType == '2') {
			this.getCode();
		}
	},

	// 微信分享
	onShareAppMessage() {
		let url = this.getPageUrl();
		return {
			title: this.articleDetail.title,
			path: url
		};
		// setTimeout( e =>{
		// 	this.showIs = '1'
		// },3000)
	},
	shareFriend() {
		//分享到微信朋友
		this.goShare('WXSceneSession');
	},
	shareFriendcricle() {
		//分享到微信朋友圈
		this.goShare('WXSenceTimeline');
	},
	methods: {
		quickInlet(e) {
			if (e == 2) {
				this.$refs.showshare.open();
			} else if (e == 1) {
				this.getList();
			}
		},
		// 保存二维码
		//微信小程序保存到相册
		handleSetting(e) {
			console.log(e);
			if (!e.detail.authSetting['scope.writePhotosAlbum']) {
				this.openSettingBtnHidden = false;
			} else {
				this.openSettingBtnHidden = true;
			}
		},
		saveEwm() {
			let that = this;
			//获取相册授权
			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.writePhotosAlbum']) {
						uni.authorize({
							scope: 'scope.writePhotosAlbum',
							success() {
								//这里是用户同意授权后的回调
								that.saveImgToLocal();
							},
							fail() {
								//这里是用户拒绝授权后的回调
								that.openSettingBtnHidden = false;
							}
						});
					} else {
						//用户已经授权过了
						that.saveImgToLocal();
					}
				}
			});
		},
		saveImgToLocal() {
			uni.showModal({
				title: '提示',
				content: '确定保存到相册吗',
				success: res => {
					if (res.confirm) {
						let that = this;
						// this.qr_path = path;
						let system_info = uni.getSystemInfoSync();
						let ctx = uni.createCanvasContext('mycanvas');
						uni.getImageInfo({
							src: that.codeList.faceurl,
							success(res) {
								console.log(res);
							  ctx.fillStyle = '#FFFFFF';
								ctx.fillRect(0, 0, 218, 218);
								ctx.drawImage(res.path, 0, 0, 203, 203);
								// ctx.fillStyle = '#FFFFFF';
								ctx.draw(true, () => {
									uni.canvasToTempFilePath({
										x: 0,
										y: 0,
										width: 203,
										height: 203,
										destWidth: 203,
										destHeight: 203,
										canvasId: 'mycanvas',
										success: function(res) {
											uni.saveImageToPhotosAlbum({
												filePath: res.tempFilePath,
												success: function() {
													uni.showToast({
														title: '图片保存成功～'
													});
												},
												fail: function(e) {},
												complete: function() {
													uni.hideLoading();
												}
											});
										},
										fail(e) {
											uni.showToast({
												title: '保存失败',
												icon: 'none'
											});
										}
									});
								});
							}
						});
					} else if (res.cancel) {
						uni.showToast({
							title: '取消成功',
							icon: 'none'
						})
					}
				}
			})
		},
		// 分享获取数据
		getCode() {
			uni.request({
				url: `${helper.requestUrl}/promote-showmycode`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res, '---');
					if (res.data.code == -1) {
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
						setTimeout(e => {
							uni.navigateBack({
								delta: 1
							});
						}, 2000);
					} else {
						this.codeList = res.data;
						this.showIs = '0';
						console.log(this.codeList.face, '*');
					}
				}
			});
		},
		// 提交申请
		getList() {
			uni.request({
				url: `${helper.requestUrl}/promote-getmycode`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res, '++++');
					if (res.data.code == 0) {
						if (res.data.tip_msg == '恭喜您,审核通过!') {
							let itemNew = res.data.memberinfos.user_setting_account;
							console.log(itemNew, 'm');
							uni.navigateTo({
								url: `/pages/getQrCode?item=${itemNew}`
							});
						} else {
							uni.navigateTo({
								url: '/pages/getQrCode'
							});
						}
					} else {
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
						setTimeout(e => {
							uni.navigateBack({
								delta: 1
							});
						}, 2000);
					}
				}
			});
		},
		cancel(type) {
			this.$refs['show' + type].close();
		},
		des() {
			uni.showToast({
				title: '请重新获取二维码',
				icon: 'none'
			});
		},
		goShare(e) {
			console.log(e);
			let sceneType = '';
			if (e == 'WXSceneSession') {
				sceneType = 'WXSceneSession';
			} else if (e == 'WXSenceTimeline') {
				sceneType = 'WXSenceTimeline';
			}
			uni.share({
				provider: 'weixin',
				scene: sceneType,
				type: 0,
				href: this.codeList.share_link,
				title: '新微金论坛',
				summary: '我正在使用新微金论坛，赶紧跟我一起来体验！',
				// summary: this.codeList.share_link,
				imageUrl: ' ',
				success: res => {
					// console.log(123)
					// uni.showToast({
					// 	title: '123',
					// 	icon:'none'
					// })
					this.showIs = '1';
				},
				fail: err => {
					// uni.showToast({
					// 	title: '321',
					// 	icon:'none'
					// })
					//      console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		// 获取当前页路径及参数,用于分享
		getPageUrl() {
			// pages/articleDetail?id=5&name=222&aaa=2344asfdasdf
			// let options = {id: '5', name: '222', aaa: '2344asfdasdf'}
			let pageNode = getCurrentPages();
			pageNode = pageNode[pageNode.length - 1];
			let url = pageNode.route;
			let options = pageNode.options;
			let optionsString = '?';
			for (let key in options) {
				optionsString += key;
				optionsString += '=';
				optionsString += options[key];
				optionsString += '&';
			}
			optionsString = optionsString.substring(0, optionsString.length - 1);
			url += optionsString;
			return url;
		},
		again() {
			this.getCode();
		}
	}
};
